.input {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__field {
    width: 100%;
    height: 32px;
    text-align: left;
    border-radius: 4px;
    padding: 6px 8px;
    border: 1px solid var(--border-color);
    font-size: 16px;
  }
}
